//.  
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" :downloadUrl="downloadUrl" />
    <Filters
      :filters="[
        { filterName: 'date', filterType: 'date', labelText: '' },
        {
          filterName: 'drl',
          filterType: 'select',
          labelText: '  ',
          isRemote: true,
          inputWidth: '260px',
          filterUrl: this.$store.state.isTestMode
            ? '../..' + '/server/drl.json'
            : '/arm-isujt' + '/server/drl.json',
          keyText: 'name',
          keyValue: 'id'
        },
        {
          filterName: 'calc',
          filterType: 'select',
          labelText: '',
          isRemote: true,
          inputWidth: '260px',
          filterUrl: this.$store.state.isTestMode
            ? '../..' + '/server/CalculationList.json'
            : '/arm-isujt' + '/server/CalculationList.json',
          keyText: 'name',
          keyValue: 'id'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <DataTable
        :headers="headers"
        :url="getUrl"
        :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
        ref="table"
        :tableType="tableType"
        :hasTabs="true"
      />
      <Buttons :params="params" />
    </div>
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import Tabs from '../../components/Tabs';
import DataTable from '../../components/DataTable';
import Buttons from '../../components/buttons/Buttons';

export default {
  name: 'ModelingNSI',
  components: {
    Title,
    Links,
    Filters,
    Tabs,
    DataTable,
    Buttons
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: 1
      });
    }
  },
  props: ['tabs'],
  computed: {
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  data() {
    return {
      panel: 0,
      defaultUrl: '/server/percent_loco.json',
      tableType: 'mod_nsi',
      downloadUrl:
        '/ajax.php?module=ExcelController&method=rep_operative_technique_pdf',
      params: {
        buttons: {
          print: true,
          download: true
        },
        name: '',
        icon: 'icon icon-trending-up'
      },
      tabModel: 3,
      headers: [
        {
          text: ' ',
          align: 'start',
          width: '250',
          textMinify: 'mini',
          value: 'section_id',
          rightBorder: true,
          fixedColumn: true
        },
        {
          text: ' \n ',
          align: 'start',
          value: 'start_date',
          textMinify: 'mini'
        },
        {
          text: ' \n ',
          align: 'start',
          value: 'end_date',
          textMinify: 'mini'
        },
        {
          text: ' \n ',
          align: 'start',
          value: 'local_speed',
          textMinify: 'mini',
          editable: true
        },
        {
          text: ' \n ',
          align: 'start',
          value: 'local_speed2',
          textMinify: 'mini'
        },
        {
          text: ' \n ',
          align: 'start',
          value: 'passenger_train',
          textMinify: 'mini',
          editable: true
        },
        {
          text:
            '. \n  \n . ',
          align: 'start',
          value: 'loc_need',
          textMinify: 'mini',
          editable: true
        },
        {
          text:
            '  \n , \n () \n   ',
          align: 'start',
          value: 'some',
          textMinify: 'mini'
        },
        {
          text:
            '   \n ,  \n () \n ',
          align: 'start',
          value: 'some1',
          textMinify: 'mini'
        },
        {
          text:
            ',   \n.  ,\n   \n ',
          align: 'start',
          value: 'some2',
          textMinify: 'mini'
        },
        {
          text:
            ',   \n  \n,   \n  ',
          align: 'start',
          value: 'some3',
          textMinify: 'mini'
        },
        {
          text: '%  \n  ',
          align: 'start',
          value: 'percent',
          textMinify: 'mini',
          editable: true
        },
        {
          text: ' \n ',
          align: 'start',
          value: 'freight_train',
          textMinify: 'mini',
          editable: true
        },
        {
          text: ' \n \n',
          align: 'start',
          value: 'train_count_even',
          textMinify: 'mini',
          editable: true
        },
        {
          text: ' \n \n',
          align: 'start',
          value: 'train_count_odd',
          textMinify: 'mini',
          editable: true
        },
        {
          text: '\n (2)',
          align: 'start',
          value: 'pl',
          textMinify: 'mini',
          editable: true
        },
        {
          text: '\n (3)',
          align: 'start',
          value: 'pt',
          textMinify: 'mini',
          editable: true
        },
        {
          text: '\n (4)',
          align: 'start',
          value: 'pt4',
          textMinify: 'mini',
          editable: true
        },
        {
          text: '\n',
          align: 'start',
          value: 'distance',
          textMinify: 'mini',
          editable: true
        }
      ]
    };
  }
};
</script>
