                ></span>
              </li>
              <li
                v-else
                :key="report.text"
                class="report-loco-list-item report-link-style"
              >
                <router-link :to="{ path: report.path }">{{
                  report.text
                }}</router-link>
              </li>
              <ul
                v-if="report.children && report.model"
                class="report-loco-list report-loco-child-list"
              >
                <template v-for="reportChild in report.children">
                  <li
                    :to="{ path: reportChild.path }"
                    :key="reportChild.text"
                    class="report-loco-list-item report-link-style"
                  >
                    <router-link :to="{ path: reportChild.path }">{{
                      reportChild.text
                    }}</router-link>
                  </li>
                </template>
              </ul>
            </div>
          </template>
        </ul>
