<template>
  <v-row class="task-buttons">
    <v-btn
      class="go"
      color="#4D6C99"
      min-height="29px"
      small
      dark
      depressed
      @click="sendTaskToTable()"
      :ripple="false"
    >
      
    </v-btn>
    <v-btn
      class="approve"
      color="#4D6C99"
      min-height="29px"
      small
      dark
      depressed
      :ripple="false"
      v-if="!complete"
    >
      
    </v-btn>
  </v-row>
</template>

<script>
export default {
  name: 'TaskButtons',
  props: ['complete', 'content'],
  methods: {
    sendTaskToTable() {
      this.$store.commit('setTaskTransitionProps', this.content);
      this.$store.commit('setFilterValues', {
        filter: 'date',
        val: [
          '',
          this.$store.state.taskTransitionProps['recieve_time'].substr(0, 10)
        ]
      });
      if (this.content.transition.category === 'normalization')
        this.$router.push('/tasks_norm_section/').catch(() => {});
      if (this.content.transition.category === 'regulation')
        this.$router.push('/tasks_reg_joint/').catch(() => {});
    }
  }
};
</script>

<style scoped lang="sass">
button.go,
button.approve
  width: 128px
  padding-top: 2px !important
  font: 14px/18px $body-font-family
  letter-spacing: 0.01px
  text-transform: none
.task-buttons
  justify-content: space-between
</style>
