//. 
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" :exportUrl="createExportUrl" />
    <Filters
      :filters="[
        { filterName: 'date', filterType: 'date', labelText: '' },
        {
          filterName: 'drl',
          filterType: 'select',
          labelText: '  ',
          isRemote: true,
          inputWidth: '260px',
          filterUrl: this.$store.state.isTestMode
            ? '../..' + '/server/drl.json'
            : '/arm-isujt' + '/server/drl.json',
          keyText: 'name',
          keyValue: 'id'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <Tabs
      :tableTypeArray="tabs || $store.getters.tabs.normalization"
      :tabModel="tabModel"
    />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <DataTable
        :headers="headers"
        :url="getUrl"
        :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
        ref="table"
        :tableType="tableType"
        :hasDetail="true"
        @loadDetail="setDetail"
        :hasTabs="true"
        :hasButtons="true"
      />
      <Buttons :params="params" />
    </div>
    <DetailPanel :headers="detailHeaders" ref="detailPanel" />
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import Tabs from '../../components/Tabs';
import DataTable from '../../components/DataTable';
import Buttons from '../../components/buttons/Buttons';
import DetailPanel from '../../components/DetailPanel';

export default {
  name: 'NormalizationSections',
  components: {
    Title,
    Links,
    Filters,
    Tabs,
    DataTable,
    Buttons,
    DetailPanel
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      params: {
        buttons: {
          print: true,
          export: true,
          recalc: true,
          save: true,
          cancel: true
        },
        name: ' ',
        icon: 'icon icon-trending-up'
      },
      requestForExport: 'method=norm_1&tab=panel-reportOP',
      defaultUrl: '/server/norm_1.json',
      detailHeaders: [
        {
          text: ' /',
          value: 'row_number',
          alignHeader: 'center',
          border: true
        },
        {
          text: '\n ',
          value: 'train_num',
          border: true
        },
        {
          text: '\n ',
          value: 'train_ind',
          border: true
        },
        {
          text: '\n ()',
          value: 'short_oper',
          border: true
        },
        {
          text: ' \n ()',
          value: 'time_oper',
          border: true
        },
        {
          text: '\n ',
          value: 'location',
          border: true
        }
      ],
      tabModel: 0,
      tableType: 'norm_sec',
      headers: [
        {
          text: ' ',
          align: 'start',
          textMinify: 'mini',
          fixedColumn: true,
          children: [
            {
              text: '\n ',
              value: 'long_name',
              textMinify: 'mini',
              width: '200',
              border: true,
              rightBorder: true,
              fixedColumn: true
            }
          ]
        },
        { text: '\n(L,)', value: 'distance', border: true },
        { text: '.\n..', value: 'k_pgre', border: true },
        { text: '.\n...', value: 'k_pe', border: true },
        {
          text: '-\n\nN(max)',
          value: 'tr_num',
          border: true,
          editable: true,
          tooltip: true
        },
        {
          text: ' .. \n N\n(2- \n)',
          value: 'tr_heavy_num',
          border: true
        },
        { text: ',\n', value: 'duration', border: true },
        {
          text: ' ',
          value: 'way',
          cols: 15,
          alignHeader: 'center',
          children: [
            {
              text: ' ',
              alignHeader: 'center',
              children: [
                { text: '\n(4-)', value: 'ft', border: true },
                { text: '\n(3-)', value: 'ft3', border: true },
                { text: '\n(2-)', value: 'fl', border: true }
              ]
            },
            {
              text: '',
              alignHeader: 'center',
              children: [
                {
                  text: '\n(3-)',
                  value: 'locl3',
                  border: true,
                  editable: true
                },
                {
                  text: '\n(2-)',
                  value: 'locl',
                  border: true,
                  editable: true
                }
              ]
            },
            {
              text: '',
              alignHeader: 'center',
              children: [
                {
                  text: '\n(4-)',
                  value: 'pt4',
                  border: true,
                  editable: true
                },
                {
                  text: '\n(3-)',
                  value: 'pt',
                  border: true,
                  editable: true
                },
                { text: '\n(2-)', value: 'pl', border: true, editable: true }
              ]
            },
            {
              text: ', ',
              alignHeader: 'center',
              children: [
                {
                  text: '\n(4-)',
                  value: 'rt4',
                  border: true,
                  editable: true
                },
                {
                  text: '\n(3-)',
                  value: 'rt',
                  border: true,
                  editable: true
                },
                { text: '\n(2-)', value: 'rl', border: true, editable: true }
              ]
            },
            {
              text: '',
              alignHeader: 'center',
              children: [
                { text: '\n(4-)', value: 't', border: true },
                { text: '\n(3-)', value: 't3', border: true },
                { text: '\n(2-)', value: 'l', border: true }
              ]
            },
            {
              text: '',
              alignHeader: 'center',
              children: [{ text: '', value: 'total', border: true }]
            }
          ]
        }
      ]
    };
  },
  created() {
    this.$store.state.detailInfo.detailName =
      '    ';
  },
  computed: {
    createExportUrl() {
      return (
        'method=norm_1&tab=panel-reportOP&date=' +
        this.getFormattedDate(this.$store.state.filtersValues.date)
      );
    },
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: this.$store.state.filtersValues.drl
      });
    },
    setDetail(url) {
      this.detailUrl = url;
      // this.$refs.detailPanel.updateDetail(this.detailUrl);
      this.$refs.detailPanel.updateDetail(
        this.$store.state.isTestMode
          ? '../..' + '/server/analize_details.json'
          : '/arm-isujt' + '/server/analize_details.json'
      ); //  
    }
  }
};
</script>
