//. 
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" :exportUrl="createExportUrl" />
    <Filters
      :filters="[{ filterName: 'date', filterType: 'date', labelText: '' }]"
      @reloadTable="updateTable()"
    />
    <Tabs
      :tableTypeArray="tabs || $store.getters.tabs.regulation"
      :tabModel="tabModel"
    />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <DataTable
        :headers="headers"
        :url="getUrl"
        :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
        ref="table"
        :tableType="tableType"
        :hasTabs="true"
      />
      <Buttons :params="params" :exportUrl="createExportUrl" />
    </div>
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import Tabs from '../../components/Tabs';
import DataTable from '../../components/DataTable';
import Buttons from '../../components/buttons/Buttons';

export default {
  name: 'RegulationJoints',
  components: {
    Title,
    Links,
    Filters,
    Tabs,
    DataTable,
    Buttons
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: 1
      });
    }
  },
  computed: {
    createExportUrl() {
      return (
        'method=regulation&tab=region-view&date=' +
        this.getFormattedDate(this.$store.state.filtersValues.date)
      );
    },
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      dateValue: Math.floor(Date.now() / 1000),
      defaultUrl: '/server/splices.json',
      tableType: 'reg_join',
      tabModel: 0,
      params: {
        buttons: {
          print: true,
          export: true
        },
        name: ' ',
        icon: 'icon icon-settings'
      },
      headers: [
        {
          text: ' /',
          align: 'start',
          value: 'sort_number',
          textMinify: 'mini',
          border: true
        },
        {
          text: '  ',
          align: 'start',
          value: 'long_name',
          textMinify: 'mini',
          border: true,
          rightBorder: true
        },
        {
          text: '-  ',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 6:00 ',
              value: 'plan_train_half',
              alignHeader: 'center',
              border: true,
              editable: true
            },
            {
              text: ' 18:00 ',
              value: 'plan_train',
              alignHeader: 'center',
              border: true,
              editable: true
            }
          ]
        },
        {
          text: '-  ',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 6:00 ',
              value: 'plan_loco_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: ' 18:00 ',
              value: 'plan_loco',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: '-  -',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 6:00 ',
              value: 'plan_train_loco_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: ' 18:00 ',
              value: 'plan_train_loco',
              alignHeader: 'center',
              border: true
            }
          ]
        }
      ]
    };
  }
};
</script>
