//. 
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" :exportUrl="createExportUrl" />
    <Filters
      :filters="[{ filterName: 'date', filterType: 'date', labelText: '' }]"
      @reloadTable="updateTable()"
    />
    <Tabs
      :tableTypeArray="tabs || $store.getters.tabs.regulation"
      :tabModel="tabModel"
    />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <DataTable
        :headers="headers"
        :url="getUrl"
        :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
        ref="table"
        :tableType="tableType"
        :hasTabs="true"
      />
      <Buttons :params="params" />
    </div>
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import Tabs from '../../components/Tabs';
import DataTable from '../../components/DataTable';
import Buttons from '../../components/buttons/Buttons';

export default {
  name: 'RegulationSections',
  components: {
    Title,
    Links,
    Filters,
    Tabs,
    DataTable,
    Buttons
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: 1
      });
    }
  },
  computed: {
    createExportUrl() {
      return (
        'method=regulation&tab=region-view&date=' +
        this.getFormattedDate(this.$store.state.filtersValues.date)
      );
    },
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      defaultUrl: '/server/regions.json',
      tabModel: 1,
      params: {
        buttons: {
          print: true,
          export: true
        },
        name: ' ',
        icon: 'icon icon-settings'
      },
      tableType: 'reg_sec',
      headers: [
        {
          text: '',
          align: 'start',
          width: '250',
          value: 'long_name',
          textMinify: 'mini',
          border: true,
          fixedColumn: true,
          rightBorder: true
        },
        {
          text: '. ,\n ..',
          align: 'start',
          value: 'oper_norm_calc',
          border: true
        },
        {
          text: '%  ',
          align: 'start',
          value: 'total_percent',
          border: true
        },
        {
          text: ' \n  18:00 .,\n..',
          align: 'start',
          value: 'existence_forecast',
          border: true
        },
        {
          text: ' \n  18:00 .,\n..',
          align: 'start',
          value: 'existence_fact',
          border: true
        },
        {
          text: '-\n , ',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 18:01\n 06:00 ',
              value: 'plan_odd_direction_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: ' 18:01\n 18:00 ',
              value: 'plan_odd_direction',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: '-\n , ',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 18:01\n 06:00 ',
              value: 'plan_even_direction_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: ' 18:01\n 18:00 ',
              value: 'plan_even_direction',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: '  ,\n..',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 18:01\n 06:00 ',
              value: 'staging_for_repairs_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: ' 18:01\n 18:00 ',
              value: 'staging_for_repairs',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: '  \n , ..',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 18:01\n 06:00 ',
              value: 'work_from_repairs_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: ' 18:01\n 18:00 ',
              value: 'work_from_repairs',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: ' .\n . , ..',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 18:01\n 06:00 ',
              value: 'dislocation_forecast_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: ' 18:01\n 18:00 ',
              value: 'dislocation_forecast',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: '  . ,\n..',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 18:01\n 06:00 ',
              value: 'next_day_plan_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: ' 18:01\n 18:00 ',
              value: 'next_day_plan',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: '  .  (. \n .), ..',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 18:01\n 06:00 ',
              value: 'next_day_plan_half_data_fact',
              alignHeader: 'center',
              border: true
            },
            {
              text: ' 18:01\n 18:00 ',
              value: 'next_day_plan_data_fact',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: ' / ,\n..',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 18:01\n 06:00 ',
              value: 'deficit_excess_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: ' 18:01\n 18:00 ',
              value: 'deficit_excess',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: '',
          align: 'end',
          alignHeader: 'center',
          value: 'splice_name',
          border: true
        },
        {
          text: '.  \n , ',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 18:01\n 06:00 ',
              value: 'reg_task_odd_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: ' 18:01\n 18:00 ',
              value: 'reg_task_odd',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: '.  \n , ',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: ' 18:01\n 06:00 ',
              value: 'reg_task_even_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: ' 18:01\n 18:00 ',
              value: 'reg_task_even',
              alignHeader: 'center',
              border: true
            }
          ]
        }
      ]
    };
  }
};
</script>
