//. 
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" :downloadUrl="downloadUrl" />
    <Filters
      :filters="[
        { filterName: 'date', filterType: 'date', labelText: '' },
        {
          filterName: 'drl',
          filterType: 'select',
          labelText: '  ',
          isRemote: true,
          inputWidth: '260px',
          filterUrl: this.$store.state.isTestMode
            ? '../..' + '/server/drl.json'
            : '/arm-isujt' + '/server/drl.json',
          keyText: 'name',
          keyValue: 'id'
        },
        {
          filterType: 'choose_btn'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <DataTable
        :headers="headers"
        :url="getUrl"
        :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
        ref="table"
        :tableType="tableType"
        :hasTabs="true"
      />
      <Buttons :params="params" />
    </div>
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import Tabs from '../../components/Tabs';
import DataTable from '../../components/DataTable';
import Buttons from '../../components/buttons/Buttons';
export default {
  name: 'ModelingComparison',
  components: {
    Title,
    Links,
    Filters,
    Tabs,
    DataTable,
    Buttons
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: 1
      });
    }
  },
  props: ['tabs'],
  computed: {
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  data() {
    return {
      panel: 0,
      defaultUrl: '/server/compare.json',
      tableType: 'mod_compare',
      downloadUrl:
        '/ajax.php?module=ExcelController&method=rep_operative_technique_pdf',
      params: {
        buttons: {
          print: true,
          download: true
        },
        name: '',
        icon: 'icon icon-trending-up'
      },
      tabModel: 4,
      headers: [
        {
          text: '',
          align: 'start',
          width: '250',
          value: 'section_id',
          rightBorder: true,
          fixedColumn: true
        },
        { text: '\n(L,)', value: 'distance', border: true },
        { text: '.\n..', value: 'k_pgre', border: true },
        { text: '.\n...', value: 'k_pe', border: true },
        {
          text: '-\n\nN (max)',
          value: 'tr_num',
          border: true,
          editable: true,
          tooltip: true
        },
        {
          text: ' .. \n N\n(2- \n)',
          value: 'tr_heavy_num',
          border: true
        },
        { text: ',\n', value: 'duration', border: true },
        {
          text: ' ',
          value: 'way',
          cols: 15,
          alignHeader: 'center',
          children: [
            {
              text: ' ',
              alignHeader: 'center',
              children: [
                { text: '\n(4-)', value: 'ft', border: true },
                { text: '\n(3-)', value: 'ft3', border: true },
                { text: '\n(2-)', value: 'fl', border: true }
              ]
            },
            {
              text: '',
              alignHeader: 'center',
              children: [
                {
                  text: '\n(3-)',
                  value: 'locl3',
                  border: true,
                  editable: true
                },
                {
                  text: '\n(2-)',
                  value: 'locl',
                  border: true,
                  editable: true
                }
              ]
            },
            {
              text: '',
              alignHeader: 'center',
              children: [
                {
                  text: '\n(4-)',
                  value: 'pt4',
                  border: true,
                  editable: true
                },
                {
                  text: '\n(3-)',
                  value: 'pt',
                  border: true,
                  editable: true
                },
                { text: '\n(2-)', value: 'pl', border: true, editable: true }
              ]
            },
            {
              text: ', ',
              alignHeader: 'center',
              children: [
                {
                  text: '\n(4-)',
                  value: 'rt4',
                  border: true,
                  editable: true
                },
                {
                  text: '\n(3-)',
                  value: 'rt',
                  border: true,
                  editable: true
                },
                { text: '\n(2-)', value: 'rl', border: true, editable: true }
              ]
            },
            {
              text: '',
              alignHeader: 'center',
              children: [
                { text: '\n(4-)', value: 't', border: true },
                { text: '\n(3-)', value: 't3', border: true },
                { text: '\n(2-)', value: 'l', border: true }
              ]
            },
            {
              text: '',
              alignHeader: 'center',
              children: [{ text: '', value: 'total', border: true }]
            }
          ]
        }
      ]
    };
  }
};
</script>

<style scoped></style>
