<template>
  <div class="mr-5">
    <p class="filter-label">{{ labelText }}</p>
    <v-text-field
      solo
      flat
      hide-details
      :value="value"
      :append-icon="isSearchInput ? 'mdi-magnify' : ''"
      @input="searchTextInput"
    ></v-text-field>
  </div>
</template>

<script>
export default {
  name: 'TextfieldFilter',
  props: ['labelText', 'filterType'],
  data() {
    return {
      isSearchInput: this.filterType === 'search_input'
    };
  },
  computed: {
    value: function () {
      return this.isSearchInput ? this.$store.state.filtersValues.search : '';
    }
  },
  methods: {
    searchTextInput(val) {
      this.$store.commit('setFilterValues', {
        filter: 'search',
        val
      });
    }
  }
};
</script>

<style>
.v-input input {
  max-height: 17px;
  color: #00194d !important;
}

.theme--light.v-input input {
  color: #00194d !important;
  vertical-align: middle;
  max-height: 16px;
}
</style>

<style scoped lang="sass">
.theme--light
  .v-input
    width: 120px
    margin-bottom: 15px
    padding-bottom: 4px
    padding-top: 4px
    vertical-align: middle
    border: 1px solid #7387A0
    transition: border-color $transition-fast
    &:hover
      border-color: $color-dark-blue
    button
      margin-top: -5px

.filter-label
  white-space: pre
  margin-bottom: 7px
  font: 16px/18px $font-bold
  color: $color-dark-blue

.rep_9, .rep_15, .rep_18, .rep_20

  .v-input.v-text-field
    width: 260px
    height: 26px
    align-items: center
</style>
